import Vue from 'vue'
import Vuex from 'vuex'
// 引入axios
import Axios from 'axios'
Axios.defaults.baseURL = 'http://101.37.33.51:8888'
// 请求拦截器
Axios.interceptors.request.use(function (config) {
  // code   (config.headers  ---请求头对象)
  let token = window.sessionStorage.getItem('token')
  if (!token) return config
  config.headers.authorization = token
  return config
})
Vue.prototype.$http = Axios
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    USER_ID:"",
    // 图片查看器的URL和开关
    appbigUrl: '',
    appimgflage: false,
    // 页面加载开关
    loadingflage: false,
    // 导航数据
    allnavs: [],
    // 某个用户的单独数据
    ruserData:{},
    // 用户数据
    loginUserData:{
      username:"",
      phone:"",
      password:"",
      head_img:null
    },
    // 验证是否是登录状态
    isLogin:false,
    isPC:navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i) ? false:true
  },
  mutations: {
    // 设置图片查看器的值
    setAppimgFlage(state, val) {
      state.appimgflage = val[0]
      if (!val[0]) return
      state.appbigUrl = val[1]
    },
    // 设置页面加载开关
    setLoadingFlage(state, val) {
      state.loadingflage = val
    },
    // 设置导航数据
    setAllnavs(state,arr){
      state.allnavs = arr
    },
    // 将某用户的 所有数据放入对应的位置
    setUserData(state,data){
      // 保存用户ID
      state.USER_ID = data.userId
      // 保存用户导航
      state.allnavs = data.navs
    },
    // 设置用户的数据
    loginUserData(state,data){
      // 设置数据
      state.loginUserData.username = data.loginuser.username
      state.loginUserData.phone = data.loginuser.phone
      state.loginUserData.head_img = data.loginuser.head_src
      state.loginUserData.password = data.loginuser.password
      // 保存用户ID
      state.USER_ID = data.loginuser.id
      // 登录了
      state.isLogin =true
    },
    // 设置登录状态
    setIsLogin(state,val){
      state.isLogin = val
    }
  },
  actions: {
    // 获取导航数据
    getAllnavs({commit},fn) {
      return new Promise((res) => {
        // 请求数据
        Axios.get('/navs').then((data) => {
          if(data.data.ok == 1) {
            commit('setAllnavs',data.data.data)
          }
          // 回调函数
          res(data.data)
         if(fn) fn()
        })
      })
    },
    // 获取某个用户的数据
    getRuserData({commit},obj){
      Axios.post('/ruser',obj).then((data)=>{
        // 判断是否查到人
        let res = data.data
        if(res.ok == 0)  return obj.fn()
        commit('setUserData',res)
      })
      
    },
    // 获取登录用户的xinx 
    getLoginUserData({commit}){
      Axios.post('/users/getuserbytoken').then(({data:res}) => {
        commit('loginUserData',res)
      })
    }
  },
  modules: {
  }
})
